.container,
.container-sm,
.container-xs {
  width: 100%;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
}

.container {
  max-width: 1128px;
}

.container-sm {
  max-width: 944px;
}

.container-xs {
  max-width: 668px;
}

[class*=container] [class*=container] {
  padding-left: 0;
  padding-right: 0;
}
[class*=container] .container-sm {
  max-width: 896px;
}
[class*=container] .container-xs {
  max-width: 620px;
}

body {
  font-family: "Source Sans Pro", Helvetica, Verdana, sans-serif;
}

.center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-content: center;
}

.header-background {
  border: none;
  width: 100%;
  /* background-image: inherit; */
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: rgb(105, 103, 103);
  background-image: url("/src/assets/img//homepage/1.png");
}

.header-title {
  font-family: Norwester;
  text-align: center;
  font-size: 8.2vw;
  padding: 5vh;
}
.header-title span {
  margin: 1.5vw;
}

.header-paragraph {
  text-align: center;
  font-size: 3.7vw;
}

@media screen and (max-width: 856px) {
  .header-background {
    min-height: 55vw;
  }
  .header-title {
    font-size: 10vw;
  }
  .header-paragraph {
    font-size: 4vw;
  }
}
/* Footer */
footer {
  overflow: hidden;
  font-family: digital-7;
  background-color: #CF5609;
  border-top: 5px solid white;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.1);
  padding: 2.5vw;
  margin-top: 2.5vw;
}

#footer-relative {
  position: relative;
  bottom: 0;
}

#footer-bottom {
  position: fixed;
  margin-top: 90%;
  bottom: 0;
}

/* Social media icon links*/
.foot-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.foot-wrapper .icon-button {
  display: inline-block;
  height: 6vw;
  width: 6vw;
  margin: 0 1vw 0 1vw;
  overflow: hidden;
  background: rgb(59, 59, 59);
  border-radius: 4vw;
  cursor: pointer;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-out;
}

.foot-wrapper .icon-button .icon {
  display: inline-block;
  color: white;
  height: 6vw;
  width: 6vw;
  text-align: center;
  border-radius: 50%;
  box-sizing: border-box;
  line-height: 70px;
  transition: all 0.3s ease-out;
}

.foot-wrapper .icon-button .icon i {
  font-size: 3.5vw;
  line-height: 6vw;
  transition: all 0.3s ease-out;
}

.foot-wrapper .icon-button span {
  font-size: 2.5vw;
  line-height: 70px;
  margin: 0 1vw 0 1vw;
  transition: all 0.3s ease-out;
}

.icon-span {
  color: white;
}

.icon-anchor {
  color: white;
  text-decoration: none;
}

.tableau-icon {
  font-size: 3.5vw;
  line-height: 6vw;
  width: 3.5vw;
  margin: 1vw auto 0 auto;
  color: #fff;
  transition: all 0.3s ease-out;
}

@media (hover: hover) {
  .foot-wrapper .icon-button:hover {
    width: 20vw;
  }
  .foot-wrapper .icon-button:nth-child(1):hover .icon {
    background: rgba(12, 12, 12, 0.404);
  }
  .foot-wrapper .icon-button:nth-child(2):hover .icon {
    background: rgba(12, 12, 12, 0.404);
  }
  .foot-wrapper .icon-button:nth-child(3):hover .icon {
    background: rgba(12, 12, 12, 0.404);
  }
  .foot-wrapper .icon-button:nth-child(4):hover .icon {
    background: rgba(12, 12, 12, 0.404);
  }
  .foot-wrapper .icon-button:hover .icon i {
    color: #fff;
  }
}
@media screen and (max-width: 856px) {
  .foot-wrapper .icon-button {
    height: 15vw;
    width: 15vw;
    border-radius: 50%;
    margin: 0 1vw 0 1vw;
  }
  .foot-wrapper .icon-button .icon {
    height: 15vw;
    width: 15vw;
    line-height: 15vw;
    transition: none;
  }
  .foot-wrapper .icon-button .icon i {
    font-size: 9vw;
    line-height: 15vw;
  }
  .foot-wrapper .icon-button span {
    display: none;
    transition: none;
  }
  .tableau-icon {
    height: 9vw;
    width: 9vw;
    margin: 2vw auto 0 auto;
    line-height: 15vw;
    transition: none;
  }
}
/* navbar */
.desktop-navigation {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  align-content: center;
  z-index: 1;
  padding: 2px;
  height: 5vw;
  border-bottom: solid #CF5609;
  border-width: 0.5vw;
  background-color: rgba(12, 12, 12, 0.404);
}

.links {
  font-family: Norwester;
  float: right;
  padding: 0;
  margin: 0;
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.links li {
  list-style: none;
}
.links a {
  display: inline-block;
  margin: 0 2px 0 2px;
  font-size: 2.2vw;
  font-weight: bold;
  color: #fff;
  text-decoration: none;
}

#active {
  color: #CF5609;
}

.mobile-links {
  display: none;
}

.icon-burger {
  display: none;
  position: absolute;
  right: 5%;
  top: 50%;
  transform: translateY(-50%);
}

.icon-burger .line {
  width: 35px;
  height: 5px;
  background-color: #fff;
  margin: 3.5px;
  border-radius: 3px;
  transition: all 0.3s ease-in-out;
}

@media screen and (max-width: 856px) {
  /* navbar */
  .desktop-navigation {
    display: none;
  }
  .icon-burger {
    display: flex;
    z-index: 2;
    position: relative;
    margin-top: 13vw;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    flex-wrap: nowrap;
    align-content: center;
  }
  .icon-burger .line {
    background-color: #CF5609;
  }
  .mobile-links {
    display: flex;
    font-family: Norwester;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    position: fixed;
    z-index: 1;
    left: 0;
    right: 0;
    top: 2.5vh;
    bottom: 0;
    width: auto;
    height: auto;
    background-color: rgba(0, 0, 0, 0.8);
    overflow: hidden;
    box-sizing: border-box;
    padding: 0;
    transition: all 0.5s ease-in-out;
  }
  .mobile-links li {
    list-style: none;
  }
  .mobile-links a {
    text-decoration: none;
    color: white;
    font-size: 7vw;
  }
}
